@import url("https://fonts.googleapis.com/css?family=Inconsolata");

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

input {
  line-height: normal;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  font-size: 62.5%;
  /* 62.5% of 16px = 10px */
}

body {
  font-size: 1.6rem;
  line-height: 2.2rem;
  background: url("data: image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAACCAMAAACuX0YVAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAAZQTFRFAAAA////pdmf3QAAAAJ0Uk5T/wDltzBKAAAADklEQVR42mJgYGAECDAAAAUAAoeC334AAAAASUVORK5CYII=") top left repeat, rgba(0, 0, 0, 0.9);
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  font-family: Inconsolata, monospace;
  font-weight: 600;
  margin: 1.5rem 0;
}

h1 {
  font-size: 3.2rem;
  line-height: 3.8rem;
}

h2 {
  font-size: 2.4rem;
  line-height: 3rem;
}

h3 {
  font-size: 2.2rem;
  line-height: 2.8rem;
}

h4 {
  font-size: 1.8rem;
  line-height: 2.6rem;
}

h5 {
  font-size: 1.6rem;
  line-height: 2.2rem;
}

h6 {
  font-size: 1.4rem;
  line-height: 2rem;
}

body,
body .a {
  word-break: break-word;
  white-space: normal;
  word-wrap: break-word;
}

body a:not(.link--no-suffix) {
  background: url("data: image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAMAAAC67D+PAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAA9QTFRFzs7OqKio0NDQ////////DlgqrAAAAAV0Uk5T/////wD7tg5TAAAAN0lEQVR42izLyQ0AMAgDQYPpv+b4yD7QEBGcYoI0yN454Sqy9KnfTUufQpB7c/9AM1RlE58AAwBEcAE4zo3mOAAAAABJRU5ErkJggg==") no-repeat scroll right center transparent;
  padding-right: 13px;
  outline: none;
  color: #fff;
  cursor: pointer;
  text-decoration: underline;
  font-weight: 700;
}

body hr {
  margin: 0;
  clear: both;
}

main {
  padding: 3rem 1.5rem 1.5rem 1.5rem;
}

header {
  padding: 1.5rem 1.5rem 3rem 1.5rem;
  background: #1d1a1a;
}

header > div section > p {
  margin: 0.5rem 0;
}

header > div aside ul {
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 0;
  margin: 0;
}

header > div aside li a {
  display: block;
}

header > div aside li:not(:last-child) a {
  margin-right: 1.5rem;
}

header > div aside li svg {
  height: 3rem;
  width: auto;
  fill: #fff;
}

@media only screen and (min-width: 768px) {
  header > div {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 767px) {
  header > div {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
  }

  header > div aside {
    margin-top: 4rem;
  }
}

.terminal {
  min-height: 100%;
  position: relative;
  width: 100%;
}

.terminal ::selection {
  background: #0080ff;
}

.terminal .container {
  position: relative;
}

.terminal .container output {
  clear: both;
  width: 100%;
}

.terminal .container output pre {
  margin: 0;
}

.terminal .container output table .input-line {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0;
  padding: 0;
  width: 100%;
}

.terminal .container output table .input-line td {
  margin: 0;
  padding: 0;
}

.terminal .container output table .input-line td:last-child {
  padding-left: 1px;
}

.terminal .cmdline {
  background-color: transparent;
  border: none;
  color: inherit;
  font: inherit;
  margin: 0;
  outline: none;
  width: 100%;
}

.terminal .hidden {
  display: none;
}

.terminal .prompt {
  line-height: 2rem;
  margin-right: 7px;
  white-space: nowrap;
}

.terminal.terminal-interlaced {
  color: white;
  font-family: Inconsolata, monospace;
}

.terminal.terminal-interlaced a {
  color: #fff;
}

.terminal.terminal-interlaced a.external {
  background: url("data: image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAMAAAC67D+PAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAA9QTFRFzs7OqKio0NDQ////////DlgqrAAAAAV0Uk5T/////wD7tg5TAAAAN0lEQVR42izLyQ0AMAgDQYPpv+b4yD7QEBGcYoI0yN454Sqy9KnfTUufQpB7c/9AM1RlE58AAwBEcAE4zo3mOAAAAABJRU5ErkJggg==") no-repeat scroll right center transparent;
  padding-right: 13px;
  outline: none;
}

.terminal.terminal-interlaced a .background {
  background: url("data: image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAACCAMAAACuX0YVAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAAZQTFRFAAAA////pdmf3QAAAAJ0Uk5T/wDltzBKAAAADklEQVR42mJgYGAECDAAAAUAAoeC334AAAAASUVORK5CYII=") top left repeat, rgba(0, 0, 0, 0.9);
  height: 100%;
  left: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%;
}

.terminal.terminal-interlaced a .background .interlace {
  background: url("data: image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAACCAMAAACuX0YVAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAAZQTFRFAAAA////pdmf3QAAAAJ0Uk5T/wDltzBKAAAADklEQVR42mJgYGAECDAAAAUAAoeC334AAAAASUVORK5CYII=") top left repeat, transparent;
  height: 100%;
  left: 0;
  opacity: 0.15;
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10;
}

.terminal.terminal-interlaced .container output pre {
  color: white;
  font-family: Inconsolata, monospace;
}

.terminal.terminal-interlaced .prompt {
  color: #fc0;
}

#terminal ul li {
  font-weight: 500;
  word-break: break-word;
  white-space: normal;
}

#terminal ul li a {
  font-weight: 700;
  font-size: inherit;
  line-height: inherit;
}

@media (max-width: 991px) {
  html {
    font-size: 12px;
  }

  h1 {
    font-size: 2.6rem;
    line-height: 3.2rem;
  }

  h2 {
    font-size: 2rem;
    line-height: 2.4rem;
  }

  h3 {
    font-size: 1.8rem;
    line-height: 2.4rem;
  }

  h4 {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
}




