@media (max-width: 991px) {
  html {
    font-size: 12px;
  }

  h1 {
    font-size: 2.6rem;
    line-height: 3.2rem;
  }

  h2 {
    font-size: 2rem;
    line-height: 2.4rem;
  }

  h3 {
    font-size: 1.8rem;
    line-height: 2.4rem;
  }

  h4 {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
}



